import {
  ChangeDetectionStrategy,
  Component,
  ElementRef,
  EventEmitter,
  Input,
  Output
} from '@angular/core';
import { Tab } from './types/tab.type';

@Component({
  selector: 'sof-tab',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <button
      *ngIf="tab?.routerLink | sofIsNullOrUndefined; else withLink"
      sofButton
      type="button"
      [icon]="tab.icon"
      [class.selected]="isSelected"
      (click)="clickTab.emit()"
    >
      <span
        class="pr-2"
        *ngIf="!(tab.count | sofIsNullOrUndefined)"
        [class.pl-2]="!(tab.icon | sofIsNullOrUndefined)"
        >{{ tab.count }}</span
      >
      <span class="title" *ngIf="!(tab.label | sofIsNullOrUndefined)">{{
        tc + '.' + tab.label | translate
      }}</span>
      <span class="title" *ngIf="!(tab.translation | sofIsNullOrUndefined)">{{
        tab.translation
      }}</span>
    </button>
    <ng-template #withLink>
      <a
        sofButton
        type="button"
        [icon]="tab.icon"
        [routerLink]="tab.routerLink"
        [queryParams]="tab.queryParams"
        routerLinkActive="selected"
        (click)="clickTab.emit()"
      >
        <span
          class="counter pr-2"
          *ngIf="!(tab.count | sofIsNullOrUndefined)"
          [class.pl-2]="!(tab.icon | sofIsNullOrUndefined)"
          >{{ tab.count }}</span
        >
        <ng-container *ngIf="!(tab.label | sofIsNullOrUndefined)">
          {{ tc + '.' + tab.label | translate }}
        </ng-container>
        <ng-container *ngIf="!(tab.translation | sofIsNullOrUndefined)">
          {{ tab.translation }}
        </ng-container>
      </a>
    </ng-template>
  `,
  styleUrls: ['./tab.component.scss']
})
export class TabComponent {
  @Input() tc: string;
  @Input() tab: Tab;
  @Input() isSelected: boolean;
  @Output() clickTab = new EventEmitter();

  constructor(public elementRef: ElementRef) {}
}
